import { Container, Col, Row, } from "react-bootstrap";
import React, { Component } from 'react';
import './FAQ.css';
import Navbar from './Navbar'

class FAQ extends Component {
    componentDidMount() {
        window.scrollTo(0, 0);
    }

    render() {
        return (
            <>
                <Navbar hide={true} />
                <div id="faq">
                    <Container className="heading">
                        <Row>
                            <Col sm>
                                <h1 style={{ textAlign: 'center', fontSize: '40px' }}>FAQ</h1><br />
                                <h2 id="commonly-asked-questions" style={{ textAlign: 'center', fontSize: '20px' }}>Commonly Asked Questions</h2>
                            </Col>
                        </Row>
                    </Container>

                    <Container className="content">
                        <Row>
                            <Col className="items">
                                <h1><a href="#commonly-asked-questions">#</a> Q1: Is ClashPerk Legal / Fair Play?</h1>
                                <h4>
                                    Yes, we always support Fair Play. <br />
                                    ClashPerk collects all the data directly from Supercell API. <br />
                                    For more info visit
                                    <a style={{ color: 'white' }} href="https://developer.clashofclans.com">
                                        https://developer.clashofclans.com
                                    </a>
                                </h4>
                            </Col>
                        </Row>

                        <Row>
                            <Col className="items">
                                <h1><a href="#commonly-asked-questions">#</a> Q2: Why the bot is not working in my server?</h1>
                                <h4>
                                    Make sure that ClashPerk has all required permissions in your Discord.
                                </h4>
                            </Col>
                        </Row>

                        <Row>
                            <Col className="items">
                                <h1><a href="#what-is-clan-verification">#</a> Q3: What is clan verification?</h1>
                                <h4>
                                    It's a security feature of the bot to ensure you are a Leader or Co-Leader in the clan
                                    and you're not trying to abuse the bot by tracking clans which you don't own.
                                </h4>

                                <strong >How to verify?</strong>

                                <ul style={{ paddingTop: '10px', paddingBottom: '10px' }}>
                                    <li>
                                        In order to verify your clan, you need to be a leader / co-leader of the clan
                                        </li>
                                    <li>
                                        Add the code CPXX at end of the clan description. (actual code will be provided by the bot)
                                        </li>
                                </ul>

                                <strong>Things to know...</strong>

                                <h4 style={{ paddingTop: '5px' }}>
                                    You should wait at least 2 min before you run the command again and the code should be removed once the command has been successfully run.
                                </h4>
                            </Col>
                        </Row>

                        <Row id="how-does-last-online-work">
                            <Col className="items">
                                <h1><a href="#how-does-last-online-work">#</a> Q4: How does last online work?</h1>
                                <h4>
                                    The bot can't see who is online in your clan. So it uses the following points to determine an approximate last online time!
                                </h4>
                                <ul>
                                    <li>
                                        Donated or Received Troops
                                    </li>
                                    <li>
                                        Gained or Lost Versus Trophies
                                    </li>
                                    <li>
                                        Player Name Change
                                    </li>
                                    <li>
                                        Increased XP Level
                                    </li>
                                    <li>
                                        Gained Legend Trophies
                                    </li>
                                    <li>
                                        Attacked in War (Coming Soon)
                                    </li>
                                </ul>
                            </Col>
                        </Row>
                        <Row id="how-does-clan-games-scoreboard-work">
                            <Col className="items" style={{ paddingTop: '5px' }}>
                                <h1><a href="#how-does-clan-games-scoreboard-work">#</a> Q5: How does Clan Games scoreboard work?</h1>
                                <h4>
                                    The Clan Games point can't be obtained directly! It's only possible by comparing <strong>Games Champion</strong> achievement. <br />
                                    Our system pulls everyone's <strong>Games Champion</strong> achievement beginning of the Clan Games and compares every 30 min.
                                </h4>
                            </Col>
                        </Row>
                    </Container>
                </div>
            </>
        );
    }
}

export default FAQ;
